#screen-horizon {
	text-align: center;
	
	display: flex;
	
	scroll-snap-type: x mandatory;
	overflow-x: scroll;
	/* overflow-y: hidden; */
	
}
#screen-horizon::-webkit-scrollbar { display: none; }


#screen-horizon > div {
	scroll-snap-align: center;
	user-select: none;
}

.guard {
	height: 10vh;
	
}