.navbar{
    position: fixed;
    display: flex;
    
    width: 100vw;
    height: 10vh;
    
    
}

.nav{
    width: 50vw;
    text-align: center;
    line-height: 10vh;
    
    font-size: 4vh;
    
    margin-left: 0px;
    padding-left: 0px;
    
    border-bottom-style: dashed;
    border-bottom-color: #282c37;
    border-bottom-width: 2px;
    
    border-bottom-left-radius: 5vw;
    border-bottom-right-radius: 5vw;
}